<template>
  
      <div class="box">
        <h3>Araç Ekle</h3>
      <form @submit.prevent="onFormSubmit">
        

          <input
            type="text"
            class="form-control"
            placeholder="Araç İsmi"
            v-model="arac.arac_ismi"
            required
          />

          <input
            type="text"
            class="form-control"
            placeholder="Araç Durumu"
            v-model="arac.arac_durumu"
            required
          />

 <button class="btn new-add-save">Kaydet</button>


      </form>
    </div>
  
</template>

<script>
import { db } from "../firebaseDb";
export default {
  data() {
    return {
      arac: {},
    };
  },
  methods: {
    onFormSubmit(event) {
      event.preventDefault();
      db.collection("araclar")
        .add(this.arac)
        .then(() => {
          // alert("User successfully created!");
          this.arac.arac_ismi = "";
          this.arac.arac_durmu = "";
          this.$router.push("/araclist");
        })
        .catch((error) => {
          console.log(error);
        });
    },
  },
};
</script>

